 import './App.css';
 import Map from "./Map"
 import React,{useState,useEffect} from "react";
 import { FormControl,Select,MenuItem,CardContent,Card} from "@material-ui/core";
import InputBox from "./InputBox";
import Table from "./Table"
import {sortData} from "./util";
function App() {
  const [countries, setcountries] = useState([]);
  const [country, setcountry] = useState("worldwide");
  const [countryInfo, setcountryInfo] = useState({});
  const [tableData, settableData] = useState([]);
  useEffect(() => {
    const getCountriesData = async () => {
      fetch("https://disease.sh/v3/covid-19/countries")
        .then((response) => response.json())
        .then((data) => {
          const countries = data.map((country) => (
            {
            name: country.country,
            value: country.countryInfo.iso2,
            
          }
          ));
          const sortDatas = sortData(data);
          settableData(sortDatas);
          setcountries(countries);
          
        });
    };
    
    getCountriesData();
  }, []);

useEffect(() => {
  fetch("https://disease.sh/v3/covid-19/all")
  .then((res)=>res.json())
  .then((data)=>setcountryInfo(data))
}, []);

  const onCountyChange=async (e)=>{
    const countryCode =e.target.value;
   
    
    const url =
      countryCode === "worldwide"
        ? "https://disease.sh/v3/covid-19/all"
        : `https://disease.sh/v3/covid-19/countries/${countryCode}`;

    await fetch(url)
    .then((response)=>response.json())
    .then((data)=>{
      setcountry(countryCode)
      setcountryInfo(data);
    })
  }
  console.log("country info",countryInfo)
console.log("country info",countryInfo.active)


  return (
    <div className="App">
      <div className="app_left">
      <div className="app_header">
      <h1>CORONA TRACKER</h1>
      <FormControl className="app_dropdown">
        <Select 
        value={country}
        variant="outlined" 
        
        onChange={onCountyChange}
        >
           {/*  <MenuItem value="worldwide">1</MenuItem>
            <MenuItem  value="worldwide">2</MenuItem>
            <MenuItem  value="worldwide">3</MenuItem> */}
            <MenuItem  value="worldwide">worldwide</MenuItem> 
            {
              countries.map(country=>{
                return <MenuItem  value={country.value}>{country.name}</MenuItem>
              })
            }
        </Select>
      </FormControl>
      </div>
      <div className="app_stats" >
       
      <InputBox title="Cases" cases={countryInfo.todayCases} total={countryInfo.cases} />
      <InputBox title="Recoverd" cases={countryInfo.todayRecovered} total={countryInfo.recovered} />
      <InputBox title="Deaths" cases={countryInfo.todayDeaths} total={countryInfo.deaths} />
      
      </div>
       
      </div>
      <div className="app_right">
        <Card>
          <CardContent>
            <h1>All CountryInfo</h1>
            <Table countries={tableData}/>
          </CardContent>
        </Card>
      </div>
    </div>
  );
}

export default App;
